<template lang="pug">
.comp-mybutton
    el-button(:size="size" :type="type" :loading="loading" :disabled="loading" @click="myClick")
        slot
</template>

<script>
export default {
    props: {
        size: '',
        type: '',
        loading: false
    },
    methods: {
        myClick () {
            if (!this.loading) {
                this.$emit('click');
            }
        }
    }
};
</script>

<style lang="scss" scoped>
.comp-mybutton {
    display: inline-block;
}
</style>



